import {col, css, row} from "../utils";

class Block {
    constructor(value , options) {
        this.value = value;
        this.options = options;
    }
}

export class TitleBlock extends Block{
    constructor(value, options) {
        super( value, options);
    }
    toHTML() {
        const {tag = "h1", styles} = this.options
        return row(col(`<${tag}>${this.value}</${tag}>`), css(styles))
    }
}

export class ImageBlock extends Block{
    constructor(value, options) {
        super( value, options);
    }
    toHTML() {
        const {styles, imageStyles: is, alt=''} = this.options
        console.log(alt, is )
        return row(`<img src='${this.value}' alt='${alt}' style='${css(is)}'/>`, css(styles))
    }
}

export class TextBlock extends Block{
    constructor(value, options) {
        super( value, options);
    }
    toHTML() {
        const {tag = "p", styles} = this.options
        return row(col(`<${tag}>${this.value}</${tag}>`), css(styles))
    }
}

export class ColumnsBlock extends Block{
    constructor(value, options) {
        super(value, options);
    }
    toHTML() {
        const {styles} = this.options
        const html = typeof this.value === "string"? this.value.split("@").map(col):this.value.map(col)
        return row(html.join(''), css(styles))
    }
}

export class BlockFromType extends Block {

    constructor(value, options) {
        super(value, options);
    }
    get text() {
        return new TextBlock(this.value, this.options)
    }
    get image() {
        return new ImageBlock(this.value, this.options)
    }
    get columns() {
        return new ColumnsBlock(this.value, this.options)
    }
    get title() {
        return new TitleBlock(this.value, this.options)
    }
}
